/* Style */

html,
body {
	height: 100%;
}

body {
	overflow-x: hidden;
}

textarea,
input {
	height: auto;
	line-height: inherit;
	padding: 0;
	color: inherit;
	background: inherit;
	display: block;
	width: auto;
	border: none;
	border-radius: 0;
	font-family: inherit;
	font-size: inherit;
	outline: none;
}

textarea,
input:focus {
	box-shadow: none;
}

[class*="svg-"] {
	color: #200e32;
}

fieldset,
label {
	margin: 0;
	padding: 0;
	border: none;
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

.animate__fadeIn {
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
    -webkit-animation-duration: 1.5s;
    animation-duration: 1.5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both
}

.container {
	min-width: 1312px;
	max-width: 1312px;
	width: 100%;
	margin: 0 auto;
	box-sizing: border-box;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(0, 0, 0, 0);
	overflow: hidden;
}

[class*="svg-"] {
	font-size: 20px;
	position: relative;
}

[class*="svg-"]:before {
	content: '';
	display: block;
}

[class*="svg-"] svg {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}

.button,
[class*="button--"] {
	display: inline-block;
	border: none;
	margin: 0;
	padding: 0;
	width: auto;
	overflow: visible;
	outline: none;
	cursor: pointer;
	background: transparent;
	border-radius: 0; /* inherit font & color from ancestor */
	font: inherit;
	color: inherit;
	text-decoration: none;
	text-align: inherit; /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
	line-height: normal; /* Corrects font smoothing for webkit */
	-webkit-font-smoothing: inherit;
	-moz-osx-font-smoothing: inherit; /* Corrects inability to style clickable `input` types in iOS */
	-webkit-appearance: none; /* for input */
	-moz-appearance: none;
	-webkit-user-select: none; /* for button */
	-moz-user-select: none;
	user-select: none; /* Remove excess padding and border in Firefox 4+ */
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	padding: 16px 21px;
	border-radius: 20px;
	border: 1px solid transparent;
	box-sizing: border-box;
	font-size: 20px;
	font-weight: 600;
	line-height: 1.6;
	text-align: center;
	color: #172239;
}

.button::-moz-focus-inner,
[class*="button--"]::-moz-focus-inner {
	border: 0;
	padding: 0;
}

.button [class*="svg-"],
[class*="button--"] [class*="svg-"] {
	position: absolute;
	top: calc(50% - -1px);
	right: 16px;
	transform: translateY(-50%);
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 16px;
	width: 1em;
	color: #200e32;
}

.button [class*="svg-"] svg,
[class*="button--"] [class*="svg-"] svg {
	fill: currentColor;
}

.button [class*="svg-"]:before,
[class*="button--"] [class*="svg-"]:before {
	padding-bottom: 100%;
}

.button {
	background-color: #ffdfd6;
}

.button--outline {
	border: solid 1px #eaeaea;
}

