.input-wrap .input-box {
	width: 100%;
	min-height: 64px;
	padding: 16px 16px 16px 66px;
	border-radius: 20px;
	border: solid 1px #eaeaea;
	box-sizing: border-box;
	resize: none;
	transition: all 250ms ease-in-out;
