.input-wrap .input-radio .input-radio {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(0, 0, 0, 0);
	overflow: hidden;
}

.input-wrap .input-radio .label {
	display: flex;
	align-items: center;
	cursor: pointer;
	padding: 24px;
	border: solid 2px #eaeaea;
	border-radius: 24px;
	font-size: 20px;
	font-weight: 600;
	line-height: 1.6;
	color: #172239;
}

.input-wrap .input-radio .label::before {
	content: '';
	display: inline-block;
	vertical-align: baseline;
	width: 32px;
	height: 32px;
	line-height: 1;
	box-sizing: border-box;
	background: #fff no-repeat center;
	border: 1px solid #eaeaea;
	flex-shrink: 0;
	transition: all 75ms cubic-bezier(0.4, 0, 1, 1);
	background-size: 14px;
	border-radius: 50%;
	margin-right: 20px;
}

.input-wrap .input-radio:hover .label:before,
.input-wrap .input-radio input:active + .label:before,
.input-wrap .input-radio input:focus + .label:before {
	border-color: #52c3ff;
	background-color: #52c3ff;
}

.input-wrap .input-radio input:checked + .label {
	border-color: #52c3ff;
}

.input-wrap .input-radio input:checked + .label::before {
	background-color: #52c3ff;
	border-color: #52c3ff;
	background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 12.8 12'><path d='M4.514 11c-.3 0-.5-.1-.7-.3l-3.1-3.2c-.4-.4-.4-1 0-1.4.4-.4 1-.4 1.4 0l2.4 2.5 6.2-6.3c.4-.4 1-.4 1.4 0 .4.4.4 1 0 1.4l-6.9 7c-.1.2-.4.3-.7.3z'/></svg>");
}

